-500;\n  }\n  \n  .btn-secondary {\n    @apply bg-gray-100 text-gray-700 hover:bg-gray-200 focus:ring-gray-500;\n  }\n  \n  .btn-success {\n    @apply bg-green-600 text-white hover:bg-green-700 focus:ring-green-500;\n  }\n  \n  .btn-danger {\n    @apply bg-red-600 text-white hover:bg-red-700 focus:ring-red-500;\n  }\n  \n  /* Card styles */\n  .card {\n    @apply bg-white rounded-lg shadow-md;\n  }\n  \n  .card-elevated {\n    @apply bg-white rounded-lg shadow-lg;\n  }\n  \n  /* Animation utilities */\n  .animate-fade-in {\n    animation: fadeIn 0.3s ease-in-out;\n  }\n  \n  .animate-fade-in-up {\n    animation: fadeInUp 0.4s ease-out;\n  }\n  \n  .animate-scale-in {\n    animation: scaleIn 0.3s ease-out;\n  }\n  \n  .animate-bounce-subtle {\n    animation: bounceSubtle 2s infinite;\n  }\n  \n  /* Hover effects */\n  .hover-lift {\n    transition: transform 0.2s ease-out;\n  }\n  \n  .hover-lift:hover {\n    transform: translateY(-2px);\n  }\n  \n  /* Focus styles for accessibility */\n  .focus-visible {\n    @apply focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2;\n  }\n  \n  /* Loading states */\n  .loading-shimmer {\n    background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);\n    background-size: 200% 100%;\n    animation: shimmer 1.5s infinite;\n  }\n  \n  /* Speech recognition states */\n  .recording-pulse {\n    animation: recordingPulse 1s ease-in-out infinite alternate;\n  }\n  \n  /* Gradient text */\n  .gradient-text {\n    @apply bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent;\n  }\n}\n\n/* Keyframe animations */\n@keyframes fadeIn {\n  from { opacity: 0; }\n  to { opacity: 1; }\n}\n\n@keyframes fadeInUp {\n  from {\n    opacity: 0;\n    transform: translateY(20px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n@keyframes scaleIn {\n  from {\n    opacity: 0;\n    transform: scale(0.9);\n  }\n  to {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n@keyframes bounceSubtle {\n  0%, 100% { transform: translateY(0); }\n  50% { transform: translateY(-4px); }\n}\n\n@keyframes shimmer {\n  0% { background-position: -200% 0; }\n  100% { background-position: 200% 0; }\n}\n\n@keyframes recordingPulse {\n  from {\n    box-shadow: 0 0 0 0 rgba(239, 68, 68, 0.4);\n  }\n  to {\n    box-shadow: 0 0 0 10px rgba(239, 68, 68, 0);\n  }\n}\n\n/* Responsive design helpers */\n@media (max-width: 640px) {\n  .mobile-friendly {\n    @apply text-base leading-relaxed;\n  }\n  \n  .mobile-button {\n    @apply py-3 px-6 text-lg;\n  }\n}\n\n/* High contrast mode support */\n@media (prefers-contrast: high) {\n  .card {\n    @apply border border-gray-300;\n  }\n  \n  .btn-primary {\n    @apply border border-blue-800;\n  }\n}\n\n/* Reduced motion support */\n@media (prefers-reduced-motion: reduce) {\n  * {\n    animation-duration: 0.01ms !important;\n    animation-iteration-count: 1 !important;\n    transition-duration: 0.01ms !important;\n    scroll-behavior: auto !important;\n  }\n}\n\n/* Dark mode preparation */\n@media (prefers-color-scheme: dark) {\n  .dark-mode-ready {\n    @apply bg-gray-900 text-white;\n  }\n}\n\n/* Print styles */\n@media print {\n  .no-print {\n    display: none !important;\n  }\n  \n  .card {\n    @apply shadow-none border border-gray-300;\n  }\n  \n  body {\n    @apply text-black bg-white;\n  }\n}\n\n/* Custom scrollbar */\n::-webkit-scrollbar {\n  width: 8px;\n}\n\n::-webkit-scrollbar-track {\n  @apply bg-gray-100;\n}\n\n::-webkit-scrollbar-thumb {\n  @apply bg-gray-300 rounded-full;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  @apply bg-gray-400;\n}\n\n/* Selection styles */\n::selection {\n  @apply bg-blue-100 text-blue-900;\n}\n\n/* Focus indicators for keyboard navigation */\n.keyboard-focus:focus {\n  @apply outline-none ring-2 ring-blue-500 ring-offset-2;\n}\n\n/* Loading skeleton styles */\n.skeleton {\n  @apply bg-gray-200 animate-pulse rounded;\n}\n\n.skeleton-text {\n  @apply h-4 bg-gray-200 animate-pulse rounded;\n}\n\n.skeleton-avatar {\n  @apply w-12 h-12 bg-gray-200 animate-pulse rounded-full;\n}\n\n/* Error state styles */\n.error-state {\n  @apply text-red-600 bg-red-50 border border-red-200 rounded-lg p-4;\n}\n\n.success-state {\n  @apply text-green-600 bg-green-50 border border-green-200 rounded-lg p-4;\n}\n\n.warning-state {\n  @apply text-yellow-600 bg-yellow-50 border border-yellow-200 rounded-lg p-4;\n}\n\n.info-state {\n  @apply text-blue-600 bg-blue-50 border border-blue-200 rounded-lg p-4;\n}\n"],"sourceRoot":""}]);
